<template>
  <div class="info">
    <HeadTop :lang="lang" @changeAppLocale="changeAppLocale" :page1="'bonus'" :titleHead="this.$t('message.f_a_3')" />

    <!-- <h1 class="textCenter title">
      <span>{{ $t("message.f_a_3") }}</span>
    </h1> -->

    <div class="line_box">
      <span v-if="hmdInfo && hmdInfo.info[7] == 0"> 0</span>
      <span v-if="hmdInfo && hmdInfo.info[7] == 1"> 1</span>
      <span v-if="hmdInfo && hmdInfo.info[7] == 2"> 2</span>
      <span v-if="hmdInfo && hmdInfo.info[7] == 3"> 3</span>
      <span v-if="hmdInfo && hmdInfo.info[7] == 4"> 4</span>
      <span v-if="hmdInfo && hmdInfo.info[7] == 5"> 5</span>
      <span v-if="hmdInfo && hmdInfo.info[7] == 6"> 6</span>
      <div class="bt"> {{ $t("message.f_43") }}</div>
      <h2>{{ hmdInfo ? hmdInfo.info[32] : "0" | figureK }} </h2>
      <!-- <h4>≈{{ hmdInfo ? (Number(hmdInfo.info[24])/ Number(hmdInfo.AESPrice))  : "0" | figure}}USDT</h4> -->
    </div>
    <button class="btn_no" @click="getReward">
      {{ $t("message.f_29_1") }}
    </button>
    <div class="cent" style="padding-top:1px">
      <div class="tab">
        <span :class="tabsNum==1?'in':''" @click="changeTab(1)">VIP1</span>
        <span :class="tabsNum==2?'in':''" @click="changeTab(2)">VIP2</span>
        <span :class="tabsNum==3?'in':''" @click="changeTab(3)">VIP3</span>
        <span :class="tabsNum==4?'in':''" @click="changeTab(4)">VIP4</span>
        <span :class="tabsNum==5?'in':''" @click="changeTab(5)">VIP5</span>

      </div>
      <div class="my">
        <van-row type="flex" justify="space-between" gutter="20">
          <van-col span="24" class="textCenter">
            <span class="textCenter">{{ $t("message.f_44") }}</span>
            <h3 class="textCenter">{{ hmdInfo ? Number(hmdInfo.info[39+tabsNum]) : "0" | figureK }} USDT</h3>
          </van-col>
          <van-col span="12">
            <span>{{ $t("message.f_45") }}</span>
            <h3>{{ hmdInfo ? Number(hmdInfo.info[34+tabsNum]) : "0" | figureK }} USDT</h3>
          </van-col>
          <van-col span="12">
            <span>{{ $t("message.f_45_r") }}</span>
            <h3>{{ hmdInfo ? Number(hmdInfo.info[44+tabsNum ]): "0" }} </h3>
          </van-col>

        </van-row>
      </div>

      <img src="../../assets/img/info/3.png" alt="" width="100%" />

      <div class="list">
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>{{ $t("message.f_46") }}</span></van-col>
          <van-col span="12" class="textRight"><span>VIP {{ hmdInfo ? hmdInfo.info[7] : "0" }}</span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>{{ $t("message.f_47") }}</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[34] : "0" | figureK }} USDT</span>
          </van-col>
        </van-row>
        <!-- <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"
                ><span>全球累计分红收益</span></van-col
              >
              <van-col span="12" class="textRight"
                ><span>{{ hmdInfo ? hmdInfo.info[34] : "0"  | figureK }} USDT</span></van-col
              >
            </van-row> -->
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>{{ $t("message.f_48") }}</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[25] : "0" }} {{ $t("message.f_50") }}</span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>{{ $t("message.f_49") }}V1</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[26] : "0" }} {{ $t("message.f_50") }}</span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>{{ $t("message.f_49") }}V2</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[27] : "0" }} {{ $t("message.f_50") }}</span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>{{ $t("message.f_49") }}V3</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[28] : "0" }} {{ $t("message.f_50") }}</span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>{{ $t("message.f_49") }}V4</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[29] : "0" }} {{ $t("message.f_50") }}</span></van-col>
        </van-row>
        <van-row type="flex" justify="space-between" class="line">
          <van-col span="12" class="textLeft"><span>{{ $t("message.f_49") }}V5</span></van-col>
          <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[30] : "0" }} {{ $t("message.f_50") }}</span></van-col>
        </van-row>
      </div>

    </div>

  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      tabsNum: 1,
      F5data: {
        allBonus: 0,
        balanceBonus: 0,
        vipLength: 0,
      },
      //   currency: "",
      link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      //   timer: null,
      //   timer2: null,
      //   show: false,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account"]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract"]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    changeTab(num){
        this.tabsNum = num;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      var url = window.location.origin + window.location.pathname + "#/";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    getReward: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.mxtContract.methods
        .getBonusReward()
        .send({ from: account })
        .then((res) => {
          console.log("getBonusReward", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          //   _this.getCoinBalance();
          //   _this.add_hmdCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getMiningInfoFun: function (num) {
      var _this = this;
      _this.tabsNum = num;
      _this.mxtContract.methods
        .getMiningInfo(_this.account)
        .call()
        .then((result) => {
          console.log(result.ct[11 + num]);
          _this.F5data.allBonus = result.ct[11 + num];
          _this.F5data.balanceBonus = result.ct[7 + num];
          _this.F5data.vipLength = result.ct[15 + num];
        });
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
          // this.getMiningInfoFun(1);
          if (_this.hmdInfo.inviteCode || _this.hmdInfo.referrer) {
            var invite = _this.hmdInfo.inviteCode || _this.hmdInfo.referrer;
            _this.getUrl(invite);
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
