      background: url(../../assets/img/vip.png) top center no-repeat;
      background-size: 100% 100%;
      width: 60px;
      height: 60px;
      border-radius: 100%;
      font-size: 24px !important;
      font-family: FZCuHuoYi-M25T;
      font-weight: 600;
      color: #FFC2EF !important;
      text-align: center;
      line-height: 60px;
      position: absolute;
      right: 5px;
