      color: #fff;
    }

    h4 {
      font-size: 14px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #fff;
    }
  }

  // .cent::before{
  //     content: '';
  //     display: inline-block;
  //     position: absolute;
  //     width: 59px;
  //     height: 85px;
  //     background: url(../../assets/img/info/1.png) top center no-repeat;
  // background-size: 100%;
  // top: 3%;
  // left: 0;
  // }
  //     .cent::after{
  //     content: '';
  //     display: inline-block;
  //     position: absolute;
  //     width: 59px;
  //     height: 85px;
  //     background: url(../../assets/img/info/2.png) top center no-repeat;
  // background-size: 100%;
  // top: 37%;
  // right: 0;
  // }
  // .qiu{position: absolute;left: 10%;}
  .cent {
    background: #0D202E;
    border-radius: 20px;
    width: 90%;
    margin: 50px auto 40px;
    border-bottom: 6px solid #153247;
    box-shadow: 0px 1px 5px #1b3b52;

    .line {
      border-bottom: 0.5px solid #000000;
    }

    .list {
      // background: #fff;
      border-radius: 0 0 15px 15px;
      padding: 20px;

      span {
        display: inline-block;
        padding: 12px 0;
      }

      .textRight span {
        font-size: 15px;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: 600;
        color: #fff;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }

    span {
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #2DF0AE;
    }

    h3 {
      font-size: 16px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #fff;
      margin-bottom: 20px;
      font-weight: bold;
      margin-top: 5px;
    }
.tab{
  width: 90%;
  margin:20px auto 0;
  display: flex;
  span{
    display: inline-block;
    padding: 10px 15px;
    font-size: 14px;
    border-radius: 10px 10px 0 0;
    color: #bbb;
  }
  .in{
     background:#091620;
     color: #2DF0AE;
  }
}
    .my {
      background: linear-gradient(#091620, #06382e);
      width: 90%;
      margin: -5px auto 20px;
      // box-shadow: 0 7px 14px #77acf7;
      // border-radius: 30px;
      padding: 30px 0 0;
      border-radius:8px;
      box-sizing: border-box;
      text-align: center;
      position: relative;
      z-index: 2;

      h3 {
        text-align: center;
      }

      span {
        display: block;
        text-align: center;

      }
    }

    .ming {
      padding: 20px 10px;

      .line {
        border-radius: 10px;
        background: #0A1923;
        padding: 15px;
        margin: 15px 0;

        h3 {
          margin-bottom: 0px !important;
