}

.top {
  text-align: center;
  padding: 20px 30px 20px;
  width: 90%;
  box-sizing: border-box;
  background: url(../../assets/img/info/2.png) top center no-repeat;
  background-size: 100% 100%;
  margin: 25px auto 20px;
  box-sizing: border-box;

  .sl {
    margin-bottom: 15px;
    text-align: center;
  }

  span {
    color: #2DF0AE;
    font-family: PingFangSC-Regular, PingFang SC;
    font-size: 12px;
    display: block;
    text-align: center;
    // margin:10px 0;
  }

  h3 {
    color: #FFCD51;


  }

  h2 {
    font-size: 24px;
    margin: 5px 0 20px;
    // margin-bottom: 12px;
    font-family: DINAlternate-Bold, DINAlternate;
    font-weight: bold;
